import { Component } from '@angular/core';
import { ActiveModalRef } from '@bmc-ux/adapt-angular';
import { IModalConfig, IPromptResponse } from './modal.config.interfaces';
import { RX_MODAL } from './modal.constant';

@Component({
  selector: 'rx-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class RxModalComponent {
  config: { modalType: string; modalConfig: IModalConfig };
  answer = '';

  constructor(public context: ActiveModalRef) {
    this.config = this.context.getData();
  }

  onConfirm(): void {
    if (this.config.modalType === RX_MODAL.modalTypes.prompt) {
      this.context.close({ response: true, answer: this.answer } as IPromptResponse);
    } else {
      this.context.close(true);
    }
  }
}
